import React, { Component, createRef } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

const DropboxSDK = require('dropbox').Dropbox;
const CLIENT_ID = '5o4k8uv2pegwprw';

class DropboxAuth extends Component {

    constructor(props) {
        super(props)

        this.state = {
            authCode: "",
            authLink: "",
            copyToClipboardText: "Copy Code"
        }
    }

    componentDidMount() {

        if (this.props.location && this.props.location.hash.indexOf("access_token") !== -1) {

            console.log("GOT IT: " + this.props.location.hash);
            let ret = {};

            this.props.location.hash.split('&').forEach(function (param) {
              var parts = param.replace(/\+/g, ' ').split('=');
              var key = parts.shift();
              var val = parts.length > 0 ? parts.join('=') : undefined;

              key = decodeURIComponent(key);

              val = val === undefined ? null : decodeURIComponent(val);

              if (ret[key] === undefined) {
                ret[key] = val;
              } else if (Array.isArray(ret[key])) {
                ret[key].push(val);
              } else {
                ret[key] = [ret[key], val];
              }
            });

            if (ret["#access_token"]) {
                this.setState({
                    authCode: ret["#access_token"],
                    authLink: ""
                })
            }

        } else {
            var dbx = new DropboxSDK({ clientId: CLIENT_ID });
            var authUrl = dbx.getAuthenticationUrl('https://d0minyk.github.io/private-location-tracker/get-auth-code');
            this.setState({ authLink: authUrl })
        }

    }

    authenticate() {

    }

    render() {

        return (
            <div className="login">
                <div className="login-container">
                    <h1>{ (this.state.authCode) ? "Successfully Authenticated" : "Authenticate Dropbox" }</h1>
                    <p>{ (this.state.authCode) ? "Copy the below code into the application" : "Get an authentication code from Dropbox that let's you to back up your locations." }</p>
                    <div className="form">
                        { (this.state.authCode) &&
                            <div className="code" style={{ wordBreak: "break-all" }}>
                                {this.state.authCode}
                            </div>
                        }
                        { (this.state.authCode) &&
                            <button style={{ width: '100%', marginTop: 15 }} onClick={() => {

                                if (navigator && navigator.clipboard) {
                                    navigator.clipboard.writeText(this.state.authCode).then(function() {
                                        this.setState({ copyToClipboardText: "Copied!" })
                                    }.bind(this), function(err) {
                                        this.setState({ copyToClipboardText: "Failed to copy!" })
                                    }.bind(this));
                                } else {
                                    this.setState({ copyToClipboardText: "Failed to copy!" })
                                }

                            }}>{this.state.copyToClipboardText}</button>
                        }
                        { (this.state.authLink) &&
                            <a style={{ width: '100%', margin: 0 }} href={this.state.authLink} className="register-btn button primary">
                                Log in
                            </a>
                        }
                    </div>
                </div>
            </div>
        )

    }

}

export default DropboxAuth;
