import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import Dashboard from './Dashboard.js';
import DropboxAuth from './DropboxAuth.js';
import Terms from './Terms.js';

class App extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div>
                <BrowserRouter>
                    <div style={{ background: 'white' }}>
                        <Switch>
                            <Route path={process.env.PUBLIC_URL + "/"} component={ Dashboard } exact></Route>
                            <Route path={process.env.PUBLIC_URL + "/terms"} component={ Terms } exact></Route>
                            <Route path={process.env.PUBLIC_URL + "/get-auth-code"} component={ DropboxAuth } exact></Route>
                            <Route path={process.env.PUBLIC_URL + "/:authcode/:encryptionkey"} component={ Dashboard } exact></Route>
                        </Switch>
                    </div>
                </BrowserRouter>
            </div>
        );
    }

}

export default App;
